import { Component, Input } from '@angular/core';

@Component({
    selector: 'login-form',
    template: `
        <div>
            <label>Username</label>
            <input [(ngModel)]="name" placeholder="Enter username"/>
            <input [(ngModel)]="pass" placeholder="Password"/>
            <label>Password</label>
        </div>
    `
})
export class LoginComponent {
    model = { name:'', pass:'' }
}


